import React from "react";

import Widget from "components/Widget/index";
import IntlMessages from "util/IntlMessages";

const ExternalCards = ({ bg, icon, data, message }) => {
  return (
    <Widget styleName={`${bg} gx-text-white gx-card-1367-p`}>
      <div className="gx-flex-row gx-justify-content-between gx-mb-2">
        <i className={`icon ${icon} gx-fs-xxl gx-mr-2`} />
        <i className="icon icon-long-arrow-right gx-fs-xxl" />
      </div>
      {
        data &&
        <h2 className="gx-text-white">{data}</h2>
      }
      <p className="gx-mb-0">
        <IntlMessages id={message} />
      </p>
    </Widget>
  );
};

export default ExternalCards;
