import React from "react";
import { Icon } from "antd";
import IntlMessages from "util/IntlMessages";
import { connect } from "react-redux";

const WelComeCard = () => {

  return (
    <div className="gx-wel-ema gx-pt-xl-2">
      <h1 className="gx-mb-3"><IntlMessages id="app.welcome" /> Alihan!</h1>
      <p className="gx-fs-sm gx-text-uppercase"> <IntlMessages id="app.you-have" /> </p>
      <ul className="gx-list-group">
        <li>
          <Icon type="message" />
          <span>5 <IntlMessages id="app.unread-messages" /></span>
        </li>
        <li>
          <Icon type="mail" />
          <span>2 <IntlMessages id="app.invitations" /></span>
        </li>
        <li>
          <Icon type="profile" />
          <span>7 <IntlMessages id="app.due-task" /></span>
        </li>
        <li>
          <Icon type="bell" />
          <span>3 <IntlMessages id="app.other-notifications" /></span>
        </li>
      </ul>
    </div>

  );
};

export default connect(({ settings }) => settings)(WelComeCard);
